.input {
	width: 100%;

	&__label {
		margin-bottom: 0.6em;

		font-weight: 500;
		font-size: 0.8em;
		line-height: 1;
		color: $mute-text-color;
	}

	&__input {
		width: 100%;

		border: none;
		border-bottom: 1px solid $main-color;

		&:focus {
			outline: none;
		}
	}

	&__error {
		font-size: 8px;
		color: $bright-color;
	}

	&_error &__input {
		color: $bright-color;
	}
}
